@import url(https://fonts.googleapis.com/css?family=Philosopher:700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/bg.63d8c12f.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

a{
  text-decoration: none;
  color: white;
  font-size: 26px;
  font-family: 'Philosopher', sans-serif;
  padding:0 10px 0 ;
}

a:hover{
  text-decoration: underline;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-list{
  display: -webkit-flex;
  display: flex;
  list-style: none;
  float: right;
  margin-right: 100px;
}

.nav-list-item{
  margin: 0 10px;
}

.container{
    float: right;
    text-align: right;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

h2{
    text-decoration: underline;
    margin-bottom: -15px;
}

.pogod{
    margin-top: 20px;
}

form{
    background-color: rgba(255, 255, 255, 0.1);
    float: right;
    margin-top: 80px;
    margin-right: 150px;
    padding: 10px 41px;
}

form input{
    font-size: 26px;
    font-family: 'Philosopher', sans-serif;
    background: transparent;
    border: none;
    outline: none;
    padding: 20px 20px 5px 20px;
    border-bottom: 1px solid rgb(253, 253, 255);
    color: rgb(250, 250, 250);
    text-transform: capitalize;
}


.weath-btn:focus{
    outline: none;
    border-bottom: 1px solid white;    
}

.weath-btn{
    padding: 8px;
    margin-left: 15px;
    color:white;
    font-size: 14px;
    cursor: pointer;
   border: none;
   background:transparent;
   border-bottom: 1px solid white;
}

.weath-btn:active{
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
}

.weather-result{
    display: none;
    font-family: 'Philosopher', sans-serif;
    color: white;
    text-align: center;
    font-size: 32px;
    width: 500px;
    background-color: rgba(255, 255, 255, 0.1);
    margin-top: 175px;
    transition: 2s;
}

.weather-icons{
    width: 50px;
    height: 50px;   
    margin: 5px 10px -15px 0;
}

.weather-list{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    list-style: none;
    padding-left: 200px;
    text-align: left;
}

.weather-list-item{
    margin-left: -50px;
    font-size: 34px;
    margin-top: 20px;
}

.weath-last-update{
    font-size: 12px;
}
